@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Inter', sans-serif;
}

body {
  background-color: white;
}

/* Blog starts */
.header {
	background-color: black;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 20px;
}

.main-title {
	color: rgb(210, 204, 204);
	font-size: 2.5em;
	letter-spacing: 3px;
  font-weight: 800;
}

.navbar > ul {
	list-style-type: none;
	margin-top: 30px;
	/* border: 1px solid yellow; */
}

.navbar > ul > li {
	display: inline;
	margin: 5px;
	padding: 5px;
  font-weight: 800;
}

.navbar > ul > li:hover {
  background-color: rgb(56, 58, 58);
	border-radius: 2px;
}

.body-card {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-top: 10px;
}

.body-card a, .navbar a {
  text-decoration: none;
	color: rgb(174, 170, 170);
	letter-spacing: 3px;
  font-weight: 900;
	font-size: .75em;
	padding: 7px;
}

/* .body-card img {
	width: 400px;
	margin-top: 30px;
	margin-bottom: 15px;
} */

.body-card .card-title {
	width: 400px;
	letter-spacing: 2px;
	font-size: 0.9em;
  font-weight: 700;
}

.card > ul {
	list-style-type: none;
	display: inline;
}

.post-type-link {
	margin-bottom: 10px;
}

.post-type-link > a {
	color: rgb(170, 167, 167);
	padding: 0;
	letter-spacing: 2px;
}

.post-type-link > a:hover {
	color: rgb(3, 3, 3);
}

.card-details > ul{
	display: inline-block;
	margin: 0;
	padding: 0;
	list-style-type: none;
	margin-top: 10px;
}

.card-details > ul > li{
	display: inline-block;
	color: rgb(42, 42, 49);
	font-size: .8em;
	font-weight: 900;
}

.card-details > ul > li > a {
	font-size: 1em;
	letter-spacing: 0.1em;
}

.card-details > ul > li > a:hover {
	color: black;
}

.card-content {
	width: 400px;
  overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	margin-top: 15px;
	letter-spacing: 0.4px;
  font-weight: 400;
	margin-bottom: 20px;
}

.load-more-button {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 50px;
}

.load-more-button > button {
	background-color: black;
	color: white;
	padding: 10px;
	width: 200px;
	cursor: pointer;
	letter-spacing: 0.2em;
}

.footer {
	position: absolute;
	background-color: black;
	width: 100%;
	color: white;
	font-family: Inter;
	padding: 30px;
	color: rgb(210, 204, 204);
	font-size: 2.5em;
	letter-spacing: 3px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.input-name {
  margin: 0;
	background-color: white;
	height: 40px;
	color: black;
	font-family: Inter;
	padding: 5px;
	border-radius: 7px;
	width: 500px;
  margin-right: 60px;
  margin-top: 4px;
}

.search-btn, .admin-save-btn {
	background-color: white;
	padding: 3px;
	width: 100px;
	cursor: pointer;
  font-weight: 700;
  line-height: 3px;
	font-family: Inter;
	height: 35px;
}

.admin-content {
	display: flex;
	border: 2px solid orangered;
	/* justify-content: space-between; */
}

.admin-title {
	width: 50rem;
	height: 3rem;
	border: 0.2px solid grey;
	border-radius: 10px;
	letter-spacing: .9px;
	padding: 20px;
	font-size: 1.1rem;
	background-color: rgb(245, 247, 247);
}

.admin-save-btn {
	border: 0;
	height: 3rem;
	border-radius: 10px;
	letter-spacing: 1.3px;
	font-size: 0.9rem;
	background-color: rgb(235, 169, 37);
}

.admin-save-btn:active {
	transform: translateY(3px);
}

.admin-editor {
	display: flex;
	border: 1px solid greenyellow;
}

.admin-editor-content {
	border: 5px solid brown;
}

/* Sidebar starts */
.sidebar {
  display: flex;
}

.sidebar .logo {
  width: 110px;
  margin-left: 20px;
  margin-top: 1px;

}

.sidebar .nav-menu {
  display: flex;
  background-color: rgb(25, 24, 59);
  width: 220px;
  height: 100vh;
  justify-content: center;
  position: fixed;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration:none;
  color:#f5f5f5;
  font-size:18px;
  width:95%;
  height:100%;
  display:flex;
  align-items:center;
  padding:16px;
  border-radius:4px;
  letter-spacing: 0.5px;
}

.nav-text:hover {
  background-color: rgb(49, 57, 100);
}

.nav-menu-items {
  margin-top: 20px;
  width: 100%;
}

.sidebar span {
  margin-left: 16px;
}

/* Prosemirror Tiptap */
Basic editor styles

.ProseMirror > * + * {
    margin-top: 0.75em;
}
.ProseMirror ul, .ProseMirror ol {
    padding: 0 1rem;
}
.ProseMirror h1, .ProseMirror h2, .ProseMirror h3, .ProseMirror h4, .ProseMirror h5, .ProseMirror h6 {
    line-height: 1.1;
}
.ProseMirror code {
    background-color: rgba(97, 97, 97, 0.1);
    color: #616161;
}
.ProseMirror pre {
    background: #a3a0a0;
    color: #fff;
    font-family: 'JetBrainsMono', monospace;
    padding: 0.75rem 1rem;
    border-radius: 0.5rem;
}
.ProseMirror pre code {
    color: black;
    padding: 0;
    background: none;
    font-size: 0.8rem;
}
.ProseMirror img {
    max-width: 100%;
    height: auto;
}
.ProseMirror blockquote {
    padding-left: 1rem;
    border-left: 2px solid rgba(13, 13, 13, 0.1);
}
.ProseMirror hr {
    border: none;
    border-top: 2px solid rgba(13, 13, 13, 0.1);
    margin: 2rem 0;
}


/* Pages start */
.dashboard,
.profile,
.posts,
.author,
